___CSS_LOADER_EXPORT___.push([module.id, `.listdown-menu {
  width: 100%;
  height: fit-content;
  box-sizing: border-box;
}
.listdown-title {
  font-size: 32px;
  font-family: "Arial, Helvetica, sans-serif";
  margin: 0px;
}
.listdown-menu ul {
  list-style-type: none; /* Remove the default bullet points */
  padding-left: 0; /* Optional: Remove left padding */
  margin: 0px;
}

.listdown-menu ul li::before {
  content: "-";
  padding: 8px; /* Space between the dash and the text */
  color: #000; /* Optional: Set the color of the dash */
}

.listdown-menu ul li {
  padding: 12px 0px;
  font-size: 12px;
}
